/* Base styles */
.blog-page {
  font-family: Arial, sans-serif; /* Your existing font family */
  max-width: 1200px; /* Your existing max-width */
  margin: 0 auto; /* Your existing margin for centering */
  padding: 20px; /* Your existing padding */
}

.page-title {
  font-size: 2rem; /* Your existing font size */
  text-align: center; /* Your existing text alignment */
  margin-bottom: 20px; /* Your existing margin */
}



.post-list {
  list-style: none;
  padding: 0;
  width: 100%;
  margin-top: 75px; /* Adjust as needed for space before the first image */
  margin-bottom: 75px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.post-item {
  width: calc(33.33% - 20px); /* Three per row on desktop */
  margin-bottom: 40px;
  position: relative;
}

.post-thumbnail-container {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.post-thumbnail {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease-in-out;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
}

.overlay:hover {
  opacity: 1;
}

.post-title {
  font-family: inherit; /* Inherit font from parent (.blog-page) */
  font-size: 1.5rem; /* Adjust font size as needed */
  font-weight: bold;
  text-align: center;
  padding: 10px;
  color: #fff; /* White color for text */
}

@media (max-width: 1024px) {
  .post-item {
    width: calc(50% - 10px); /* Two per row on tablet */
  }
}

@media (max-width: 768px) {
  .post-item {
    width: calc(50% - 10px); /* Two per row on mobile */
  }
}

@media (max-width: 480px) {
  .post-item {
    width: 100%; /* Single column on very small screens */
  }
}
